export default {
  mounted () {
    this.echartsInit()
  },
  methods: {
    echartsInit () {
      // 找到容器
      let chartDom = document.getElementById('myChart')
      let myChart = this.$echarts.init(chartDom)
      let option
      option = {
        xAxis: {
          name: '点评量', // 横坐标名称
          nameLocation: 'middle', // 名称位置
          nameTextStyle: {
            color: '#222',
            fontWeight: 'bold',
            fontSize: 18,
            align: 'center'
          },
          nameGap: 20, // 名称距离坐标的距离
          min: 0, // 横坐标最小值
          max: 20, // 横坐标最大值(该类别下有价值点评量数最大的厂商的总点评量*110%)
          splitNumber: 10, // 分割段数
          axisTick: {
            show: false // 是否显示刻度
          },
          axisLabel: {
            show: false // 是否显示刻度标签
          },
          splitLine: {
            lineStyle: {
              color: ['#aaa', '#ddd'] // 分割线颜色
            }
          }
        },
        yAxis: {
          name: '口碑值', // 横坐标名称
          nameLocation: 'middle', // 名称位置
          nameTextStyle: {
            color: '#222',
            fontWeight: 'bold',
            fontSize: 18,
            align: 'center'
          },
          nameGap: 20,
          splitNumber: 10, // 分割段数
          axisTick: {
            show: false // 是否显示刻度
          },
          axisLabel: {
            show: false // 是否显示刻度标签
          }
        },
        series: [{
          symbolSize: 20,
          data: [
            [5.0, 2.18]
          ],
          type: 'scatter',
          symbol: 'image://http://xuanxingbao.com/images/xxb_mark.png'
        },
        {
          symbolSize: 20,
          data: [
            [10.0, 8.04]
          ],
          type: 'scatter',
          symbol: 'image://http://xuanxingbao.com/images/xxb_mark.png'
        }]
      }
      option && myChart.setOption(option)
    }
  }
}
